@charset "UTF-8";
/*
 颜色值的配置-------------------------------------------------
*/
/*
 变量值的配置------------------------------------------------
*/
/*
函数配置-------------------------------------------------------
*/
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td, a, div, img {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* 这是只有在移动端才会有用 */
  -webkit-tap-highlight-color: transparent; }

body, button, input, select, textarea {
  font-family: "Helvetica Neue", Helvetica, Arial,PingFangSC-Regular, sans-serif,tahoma, arial, \5b8b\4f53;
  font-family: "Helvetica Neue", Helvetica, Arial,PingFangSC-Regular, sans-serif;
  -webkit-font-smoothing: PingFangSC-Regular;
  -moz-osx-font-smoothing: PingFangSC-Regular;
  background: #fff; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%; }

address, cite, dfn, em, var {
  font-style: normal; }

code, kbd, pre, samp {
  font-family: couriernew, courier, monospace; }

small {
  font-size: 12px; }

ul, ol, li {
  list-style: none; }

a {
  text-decoration: none;
  color: #999; }

a:hover {
  text-decoration: none; }

sup {
  vertical-align: text-top; }

sub {
  vertical-align: text-bottom; }

legend {
  color: #000; }

fieldset, img {
  border: 0; }

button, select, textarea {
  font-size: 100%;
  outline: none;
  border: 0;
  background: transparent; }

table, textarea {
  border-collapse: collapse;
  border-spacing: 0;
  resize: none; }

input, textarea {
  border: none;
  outline: none;
  resize: none;
  background: transparent; }

.clearfix {
  *zoom: 1; }

.clearfix::after {
  content: "\200B";
  display: block;
  height: 0;
  clear: both; }

.fl {
  float: left; }

.fr {
  float: right; }

.btn-bg {
  background: -webkit-linear-gradient(left, #0dafe6, #1b88fd); }

.mint-cell-wrapper {
  background-image: none !important; }

.mint-cell:last-child {
  background-image: none !important; }

.mint-cell-value > input {
  width: 4rem !important; }

.weui-cell {
  padding: 0 !important; }

@font-face {
  font-family: 'AvenirLT-Medium';
  src: url(../../static/media/sheep.8c893f1c.ttf); }

.content {
  padding: 0.66667rem 0.46667rem;
  padding-bottom: 0.66667rem;
  border-radius: 10px;
  overflow: hidden;
  background-size: 100%; }
  .content-like {
    width: 6.4rem;
    height: 6.4rem;
    margin: auto;
    margin-left: 1.33333rem; }
  .content-imgs {
    width: 10.66667rem;
    height: 17.81333rem;
    padding-bottom: 6.66667rem;
    background-size: cover;
    margin: auto;
    overflow: hidden; }
    .content-imgs .car {
      z-index: 100; }
      .content-imgs .car .bg {
        width: 8.22667rem;
        height: 15.2rem;
        margin-top: 2.32rem;
        background-size: cover;
        border-radius: 55px;
        overflow: hidden;
        margin-left: 0.29333rem; }
        .content-imgs .car .bg img {
          border-radius: 10px;
          overflow: hidden; }
  .content-happy {
    width: 9.06667rem;
    position: relative; }
    .content-happy-english {
      position: absolute;
      right: 0;
      width: 3.33333rem;
      top: -2.66667rem; }
    .content-happy-ball {
      width: 0.8rem;
      height: 0.8rem;
      position: absolute;
      left: 1.06667rem; }
    .content-happy-floow {
      width: 1.06667rem;
      height: 1.06667rem;
      position: absolute;
      right: 0;
      top: 0; }
    .content-happy-si {
      width: 100%; }
  .content-word {
    height: 10.13333rem;
    font-size: 0.53333rem;
    color: #666;
    font-family: 'AvenirLT-Medium';
    position: relative; }
    .content-word-img {
      position: absolute;
      right: 0;
      top: 0.26667rem;
      width: 2.66rem;
      height: 2.044rem; }

