@charset "UTF-8";
/*
 颜色值的配置-------------------------------------------------
*/
/*
 变量值的配置------------------------------------------------
*/
/*
函数配置-------------------------------------------------------
*/
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td, a, div, img {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* 这是只有在移动端才会有用 */
  -webkit-tap-highlight-color: transparent; }

body, button, input, select, textarea {
  font-family: "Helvetica Neue", Helvetica, Arial,PingFangSC-Regular, sans-serif,tahoma, arial, \5b8b\4f53;
  font-family: "Helvetica Neue", Helvetica, Arial,PingFangSC-Regular, sans-serif;
  -webkit-font-smoothing: PingFangSC-Regular;
  -moz-osx-font-smoothing: PingFangSC-Regular;
  background: #fff; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%; }

address, cite, dfn, em, var {
  font-style: normal; }

code, kbd, pre, samp {
  font-family: couriernew, courier, monospace; }

small {
  font-size: 12px; }

ul, ol, li {
  list-style: none; }

a {
  text-decoration: none;
  color: #999; }

a:hover {
  text-decoration: none; }

sup {
  vertical-align: text-top; }

sub {
  vertical-align: text-bottom; }

legend {
  color: #000; }

fieldset, img {
  border: 0; }

button, select, textarea {
  font-size: 100%;
  outline: none;
  border: 0;
  background: transparent; }

table, textarea {
  border-collapse: collapse;
  border-spacing: 0;
  resize: none; }

input, textarea {
  border: none;
  outline: none;
  resize: none;
  background: transparent; }

.clearfix {
  *zoom: 1; }

.clearfix::after {
  content: "\200B";
  display: block;
  height: 0;
  clear: both; }

.fl {
  float: left; }

.fr {
  float: right; }

.btn-bg {
  background: -webkit-linear-gradient(left, #0dafe6, #1b88fd); }

.mint-cell-wrapper {
  background-image: none !important; }

.mint-cell:last-child {
  background-image: none !important; }

.mint-cell-value > input {
  width: 4rem !important; }

.weui-cell {
  padding: 0 !important; }

.app {
  overflow: hidden; }
